export class App {
  constructor() {
    this.repos = [];
    this.getRepos();
  }

  getRepos() {
    require(["node-fetch"], async (fetch) => {
      const req = await fetch("https://api.github.com/users/mWalrus/repos");
      const res = await req.json();
      const result = res.map((repo) => {
        return {
          name: repo.name.replace(/-/g, " "),
          description: repo.description,
          html_url: repo.html_url,
          language: repo.language,
          languageLower: repo.language.toLowerCase(),
          // release: await this.getLatest(repo.name, fetch),
        };
      });

      this.repos = result;
    });
  }
}
